/* harmony default export */ var AddMe = ({
  name: 'app',
  data: function data() {
    return {
      first: ''
    };
  },

  firebase: {
    names: nameRef
  },

  methods: {
    submit: function submit() {
      nameRef.push({ name: this.first, edit: false });
      this.first = '';
    },
    removeName: function removeName(key) {
      nameRef.child(key).remove();
    },
    editName: function editName(key) {
      nameRef.child(key).update({ edit: true });
    },
    cancelEdit: function cancelEdit(key) {
      nameRef.child(key).update({ edit: false });
    },
    saveName: function saveName(person) {
      var key = person['.key'];
      nameRef.child(key).set({ name: person.name, edit: false });
    }
  }
});
